<template>
  <div class="card text-dark bg-light mb-3 history">
    <div class="card-header">历史订单</div>
    <div class="card-body">
      <h4 class="card-title">历史订单</h4>
      <ul v-for="(items,index) in basketData" :key="index">
        <div class="mb-2">
          <strong>订单号：{{items.id}}</strong>
        </div>
        <li v-for="(item,index) in items.basket" :key="index">
          {{item.name}}
          单价{{item.price}}元
          {{item.quantity}}个
          {{item.size}}寸
        </li>
        <div class="total mt-2">
          <strong>总计：{{items.total}}元</strong>
        </div>
        <button class="btn btn-danger btn-sm mt-3" @click="deleteBasket(items.id)">删除订单</button>
      </ul>
    </div>
  </div>
</template>


<script>
export default {
  created() {
    this.getBaskets();
  },
  computed:{
      basketData(){
        return this.$store.getters.basketData
      }
  },
  methods: {
    deleteBasket(item) {
      let _this = this;
      this.http
        .delete(`/baskets/${item}/.json`, {
          method: "DELETE",
          headers: {
            "Content-type": "application/json"
          }
        })
        .then(res => {
          this.getBaskets();
        })
    },
    getBaskets() {
      this.http.get("baskets.json").then(res => {
        let basketData = [];
        for (let item in res.data) {
          let temp = {
            id: "",
            total: "",
            basket: {}
          };
          temp.id = item;
          temp.basket = res.data[item].baskets;
          temp.total = res.data[item].total;
          basketData.push(temp);
        }
        this.$store.dispatch('getBaskets',basketData);
      });
    }
  }
};
</script>

<style scoped>
.history ul {
  margin-bottom: 20px;
  padding: 20px;
  list-style: none;
  background: #fff;
}
.history ul li {
  font-size: 14px;
  line-height: 32px;
  display: block;
}
</style>



